















import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import { ColorBoxEditorControl } from '@/app/editor/client/view/component/control';
import {
  SidebarDefaults, SidebarSettings,
} from '@/app/shared/client/type/settings/sidebar-settings';
import { Color } from '@/app/shared/client/type/color';
import { calculateActualColorDefault } from '@/app/editor/client/logic/defaults';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import useI18N from '@/app/shared/client/logic/i18n';

function useOnChangeColor(
  generalSettings: Readonly<Ref<Readonly<SidebarSettings>>>,
  emit: (event: string, settings: SidebarSettings) => void,
): (c?: Color) => void {
  function onChangeColor(value?: Color): void {
    emit('editor-action', { ...generalSettings.value, color: value });
  }
  return onChangeColor;
}

const CustomSidebarDesignEditor = defineComponent({
  components: {
    ColorBoxEditorControl,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<SidebarSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<SidebarDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      color: props.value?.color,
    }));

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value.color,
      set: useOnChangeColor(settings, emit),
    });

    return {
      i18n,
      defaultColor,
      color,
    };
  },
});

export default CustomSidebarDesignEditor;
